<template>

	<div class="member">
		<div class="cs-rylb-dailog">
			<div class="el-rylb-dialog__header"><span class="el-dialog__title">人员列表</span>

			</div>
		</div>
		<div class="cs-rylb-dailog__body">
			<el-row class="details" v-for="obj in namelist">
				<el-col :span="8"><div class="fm_name">{{obj.realname}}</div></el-col>
				<el-col :span="8"><div :class="obj.isOnline==1?'live_status':'leave_status'">{{obj.isOnline==1?"在线":"离线"}}</div></el-col>
				<el-col :span="8"><div class="_tel" v-on:click="show_contact(obj)">联系他</div></el-col>
			</el-row>
		</div>
	</div>

</template>
<script>
	export default {
		data: function() {
			return {
				memberlist: [],
				m_name: "",
				namelist: []
		  }
		},

		created: function() {
			let that=this;
			that.getList();

		},
		methods: {
			getList() {
				let that=this;
				let _token = global.data.getToken();
				this.$http.post(global.config.weburl+'api/user/queryUserList.do', {
					token:global.data.getToken(),
					type:"backUser",
					auth:"authed",
					sortby:"is_online desc, realname asc"

				}).
				then(function(res){
					console.log(res);
					if(res.data.code==0){
						var _data= res.data;
						that.memberlist=res.data.data;
						that.namelist=that.memberlist;
					} else{
						global.tools.handleError(res.data);
					}

				},function(error){

				});
			},
			show_contact(obj) {
				const Bus = global.eventHub;
				Bus.$emit('detail_contact',obj);
				this.$emit('show_contact');
			},
			f_search() {
				var that = this;
				var res = [];
				this.namelist = this.memberlist;
				//console.log(that.m_name + "1111");
				if (that.m_name) {
					this.namelist.forEach(function(item, index) {
						if (item.fm_name.indexOf(that.m_name) != -1) {
							res.push(item);
						}
					});
				} else {
					res = this.memberlist;
				}
				this.namelist = res;
			}
		}
	} 
</script>



<style>
.member{
  position: relative;
  overflow: hidden;
  width: 260px;
	box-sizing: border-box;
}
.cs-rylb-dailog__body{
	height: 400px;
	overflow-y:auto;
	overflow-x:hidden;
}
.el-rylb-dialog__header{background: #fff;padding: 10px;}
.member .listname{
  background: #f2f2f2;
  height: 45px;
  padding-left: 14px;
  line-height: 45px;
  font-size: 16px;
  font-weight: 600;
}
.member .listname input{
  width: 70px;
  height: 26px;
  border-radius: 13px!important;
  padding: 0 8px;
  outline: none;
}
.member .listname .el-icon-search{
  padding-left: 8px;
}
.member .details{
  background: #fff;
  border-top: 1px solid #e4e4e4;
  height: 45px;
  padding:0 14px;
  line-height: 45px;
  font-size: 14px;
}
.member .details .fm_name{
	width: 80px;
	height: 45px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.member .live_status{
  text-align:center ;
  color: #00CC00;
}
.member .leave_status{
  text-align:center ;
  color: #999;
}
.member ._tel{
  text-align:right ;
  color:#0099FF;
	cursor: pointer;
}
</style>



